h novelist Sarah Waters sums it up eloquently</li> <li data-v-b3458f48>In their classic book, Creativity in Business, based on a popular course they co-taught</li> <li data-v-b3458f48>Novelist and screenwriter Steven Pressfield</li> <li data-v-b3458f48>A possible off-the-wall idea or solution appears like a blip and disappears without us</li></ul> <h3 class=\"sub-title\" data-v-b3458f48>Defaulting to Mindfulness</h3> <p data-v-b3458f48>Everything along the way, to and from, fascinated her: every pebble, ant, stick, leaf, blade of grass, and crack in the sidewalk was something to be picked up, looked at, tasted, smelled, and shaken. Everything was interesting to her. She knew nothing. <br data-v-b3458f48>I knew everythingâ¦been there, done that. She was in the moment, I was in the past. She was mindful. I was mindless.</p> <blockquote class=\"blockquote\" data-v-b3458f48><p data-v-b3458f48>Our greatest weakness lies in giving up. The most certain way to succeed is always to try just one more time.</p> <footer class=\"blockquote-footer\" data-v-b3458f48><h2 class=\"title-name\" data-v-b3458f48>John Doe</h2> <h4 class=\"title-desig\" data-v-b3458f48>Project Director</h4></footer></blockquote> <p data-v-b3458f48>Both of these assumptions, of course, could be entirely false. Self-censoring is firmly rooted in our experiences with mistakes in the past and not the present. The brain messages arising from those experiences can be deceptive. </p> <div class=\"post-share-options\" data-v-b3458f48><div class=\"post-share-inner\" data-v-b3458f48><div class=\"post-title\" data-v-b3458f48>Tags:</div> <ul class=\"tags\" data-v-b3458f48><li data-v-b3458f48><a href=\"#\" data-v-b3458f48>Construction,</a></li> <li data-v-b3458f48><a href=\"#\" data-v-b3458f48>Building,</a></li> <li data-v-b3458f48><a href=\"#\" data-v-b3458f48>Structure,</a></li></ul><br data-v-b3458f48> <ul class=\"social-box\" data-v-b3458f48><li class=\"facebook\" data-v-b3458f48><a href=\"#\" data-v-b3458f48><i 